<template>

    <!-- 书城男生 -->

    <div id="Boy">

        <GeneralSwipe
            :items="items"
        ></GeneralSwipe>

        <ScripturesRecommend
            :General="General"
        ></ScripturesRecommend>

        <RankingList
            :Daily="Daily"
        >
            今日大热榜 · {{date}}
        </RankingList>

        <HighlyScore
            :High="High"
        >
            高分神作
        </HighlyScore>

        <HighlyScore
            :Exclusive="Exclusive"
        >
            独家原创
        </HighlyScore>

        <RankingList
            :Ranking="Ranking"
        >
            各类排行榜精选
        </RankingList>

        <UniversalNovelModule
            :Ancient="Ancient"
            :AncientOne="AncientOne"
        >
            都市高手
        </UniversalNovelModule>

        <UniversalNovelModule
            :Modern="Modern"
            :ModernOne="ModernOne"
        >
            东方玄幻
        </UniversalNovelModule>

    </div>

</template>

<script>
import GeneralSwipe from '@/components/GeneralSwipe.vue';
import ScripturesRecommend from '@/components/ScripturesRecommend.vue';
import RankingList from '@/components/RankingList.vue';
import HighlyScore from '@/components/HighlyScore.vue';
import UniversalNovelModule from '@/components/UniversalNovelModule.vue';


export default {
    components: {
        GeneralSwipe,
        ScripturesRecommend,
        RankingList,
        HighlyScore,
        UniversalNovelModule,
    },
    data() {
        return {
            date: "",
            items: [
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318202433629.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318412350384.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318321888182.jpg"
                },
            ],
            GeneralList: [
                "修仙万年归来",
                "极品小神医",
                "金沙古卷"
            ],
            General: [],
            DailyList: [
                "史上最强练气期",
                "荣耀王者",
                "重返1988",
                "风声",
                "一剑独尊",
                "都市潜龙"
            ],
            Daily: [],
            HighList: [
                "最强天医",
                "龙符",
                "六界封神",
                "隐秘而伟大",
                "龙回都市",
                "最佳兵王女婿",
                "都市潜龙",
                "火影开局一键满级",
            ],
            High: [],
            ExclusiveList: [
                "而立之年",
                "西游之我在天庭996",
                "鉴宝龙婿",
                "极品小医仙",
                "重生药尊归来",
                "洪荒之我真不是天道",
                "仙尊重生做赘婿",
                "天庭微信群"
            ],
            Exclusive: [],
            RankingList: [
                "南宋大相公",
                "英雄联盟之天秀中单",
                "最强小神农",
                "娱乐第一天王",
                "重生之最强人生",
                "重生大亨时代",
            ],
            Ranking: [],
            Ancient: [],
            Modern: [],
            AncientOne: {},
            ModernOne: {},
        }
    },
    activated() {

        this.$store.commit('Eliminate');

        this.$store.commit('three');
        this.date = null;
        var d = new Date();
        var n = d.getDate();
        var m = d.getMonth()
        this.date = m+1+"月"+n+"日"
    },
    created() {

        for(let i=0; i<this.GeneralList.length ; i++) {
            this.axios.get(`search?keyword=${this.GeneralList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.General.push(obj);
            })
        }

        for(let i=0; i<this.DailyList.length ; i++) {
            this.axios.get(`search?keyword=${this.DailyList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    ratio: respaonse.data.books[0].retentionRatio+"万热度",
                    id: respaonse.data.books[0]._id,
                }
                this.Daily.push(obj);
            })
        }

        for(let i=0; i<this.HighList.length ; i++) {
            this.axios.get(`search?keyword=${this.HighList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.High.push(obj);
                
            })
        }

        for(let i=0; i<this.ExclusiveList.length ; i++) {
            this.axios.get(`search?keyword=${this.ExclusiveList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.Exclusive.push(obj);
            })
        }

        for(let i=0; i<this.RankingList.length ; i++) {
            this.axios.get(`search?keyword=${this.RankingList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    ratio: respaonse.data.books[0].retentionRatio+"万热度",
                    id: respaonse.data.books[0]._id,
                }
                this.Ranking.push(obj);
            })
        }

        this.axios.get(`category-info?gender=male&major=都市`).then((respaonse)=>{
            
            this.axios.get(`book-info/${respaonse.data.books[10]._id}`).then((respaonse)=>{
                var split = respaonse.data.cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.title,
                    id: respaonse.data._id,
                    longIntro:respaonse.data.longIntro,
                    rating:respaonse.data.rating.score
                }
                this.AncientOne = obj;
            })

            for(let i=1; i<9; i++) {
                var split = respaonse.data.books[i].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[i].title,
                    id: respaonse.data.books[i]._id,
                }
                this.Ancient.push(obj);
            }
        })

        this.axios.get(`category-info?gender=male&major=玄幻`).then((respaonse)=>{

            this.axios.get(`book-info/${respaonse.data.books[0]._id}`).then((respaonse)=>{
                var split = respaonse.data.cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.title,
                    id: respaonse.data._id,
                    longIntro:respaonse.data.longIntro,
                    rating:respaonse.data.rating.score
                }
                this.ModernOne = obj;
            })

            for(let i=1; i<9; i++) {
                var split = respaonse.data.books[i].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[i].title,
                    id: respaonse.data.books[i]._id,
                }
                this.Modern.push(obj);
            }
        })

    },
}
</script>

